@import url('https://fonts.googleapis.com/css?family=Raleway:400,500');
 
* {
	 margin: 0;
	 padding: 0;
	 list-style: none;
	 vertical-align: baseline;
}
 div {
	 position: relative;
	 z-index: 2;
}
 body {
	 font-family: 'Raleway', sans-serif;
	 font-weight: 400;
	 display: flex;
	 justify-content: center;
	 align-items: center;
	 height: 100vh;
}

.wrapper {
    width: 100%;
    height: 100%;
    animation-name: hero;
    animation-duration: 1000ms;

}

.wrapperClass {
    width: 100%;
    height: 100%;
    animation-name: zero;
    animation-duration: 1000ms;

}
 .footer {
	 width: 450px;
	 text-align: center;
	 display: block;
	 margin: 15px auto 30px auto;
	 font-size: 0.8em;
	 color: #fff;
}
 .footer a {
	 font-weight: 500;
	 text-decoration: none;
	 color: #fff;
}
 #quote-box {
	 border-radius: 3px;
	 position: absolute;
	 width: 450px;
	 padding: 40px 50px;
	 display: table;
     background-color: #fff;
     opacity: 1;
     overflow: hidden;
     left: 50%;
     top: 50%;
     transform: translate(-50%,-50%);
}
 
#root {
    width: 100%;
    height: 100%;
}
 #quote-box .quote-text , #quote-box .jj{
	 text-align: center;
	 width: 450px;
	 height: auto;
	 clear: both;
	 font-weight: 500;
	 font-size: 1.75em;
}

.quote-text  {
    animation-name: hero;
    animation-duration: 500ms;
}

 #quote-box .quote-text i , #quote-box .jj i{
	 font-size: 1em;
	 margin-right: 0.4em;
}
 #quote-box .quote-author    {
	 width: 450px;
	 height: auto;
	 clear: both;
	 padding-top: 20px;
	 font-size: 1em;
     text-align: right;
     animation-name: hero;
     animation-duration: 500ms;
}
 #quote-box .buttons {
	 width: 450px;
	 margin: auto;
	 display: block;
}
 #quote-box .buttons .button {
	 height: 38px;
	 border: none;
	 border-radius: 3px;
	 color: #fff;
	 background-color: #333;
	 outline: none;
	 font-size: 0.85em;
	 padding: 8px 18px 6px 18px;
	 margin-top: 30px;
	 opacity: 1;
	 cursor: pointer;
}
 #quote-box .buttons .button:hover {
	 opacity: 0.9;
}
 #quote-box .buttons .button#tweet-quote, #quote-box .buttons .button#tumblr-quote {
	 float: left;
	 padding: 0px;
	 padding-top: 8px;
	 text-align: center;
	 font-size: 1.2em;
	 margin-right: 5px;
	 height: 30px;
	 width: 40px;
}
 #quote-box .buttons .button#new-quote {
	 float: right;
}
 
.jj {
    animation-name: zero;
    animation-duration: 500ms;
    animation-fill-mode: forwards;
}

.kk {
    text-align: right;
    animation-name: zero;
    animation-duration: 500ms;
    animation-fill-mode: forwards;
}



@keyframes zero {
    from {opacity: 1;}
    to {opacity: 0;}
  }

@keyframes hero {
    from {opacity: 0;}
    to {opacity: 1;}
}